.list {
    width: 100%;
    margin-bottom: 2em;

    
    li:nth-child(2n-1) {
        background-color: #e5e5e5;
    }

    li:last-child {
        box-shadow: 0px 5px 10px -3px rgba(0, 0, 0, 0.3);
    }

    .list-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 60px;
        padding: 0 0.75em 0 1.5em;

        .item-title-list {
            font-size: 1.2em;
        }

        .sets-delete-wrapper {
            display: flex;
            justify-content: space-between;
            width: 130px;
            .list-extra-info {
                left: 0;
                width: 110px;
            }
    
            .delete-item {
                color: #999999;
                
            }
        }

    }
}